.game {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }
  
  .card-grid {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 10px;
    width: 100%;
    max-width: 600px; /* Set a max-width to ensure the grid doesn't stretch too much */
  }
 h1{
  margin-top: 230px;
 }
  .start-button{
    margin-top: 50px;
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
  }
  .difficulty-button{
    margin-right: 10px;
    font-weight: bold;
  }
  .reset-button {
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    margin-left: -300px;
    margin-top: -90px;
    height: 80px;
    font-weight: bold;
  }
  .exit-button{
    margin-left: 900px;
    padding: 10px 20px;
   margin-top: -50px;
   font-weight: bold;
  }
  .timer, .score {
    font-size: 1.5rem;
    margin: 10px 0;
  }
  